<template>
  <div class="w-full cursor-pointer" @click="handleClickCard">
    <div class="w-full relative">
      <img :src="dataSource.img" class="w-full object-cover" />
      <div
        v-if="dataSource.onlyMember"
        class="absolute bottom-3 right-3 rounded w-20 py-1 flex items-center justify-center text-white bg-main-red"
      >
        會員限定
      </div>
    </div>
    <div class="w-full">
      <p class="w-full truncate">
        {{ dataSource.title }}
      </p>
      <p class="text-sm mt-2.5">點擊數：{{ dataSource.num }}</p>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    dataSource: {
      type: Object,
      default: () => ({}),
    },
  },
  methods: {
    handleClickCard() {
      const { id, path } = this.dataSource;
      this.$router.push(`${path}/detail/${id}`);
    },
  },
};
</script>
