<template>
  <div class="flex flex-col gap-5">
    <p class="font-medium text-3xl">
      大眼美女Ａricel讓人想回家大眼美女Ａricel讓人想回家大眼美女Ａricel讓人
    </p>
    <div class="flex flex-col justify-center items-center gap-2.5">
      <div class="w-full max-w-sm h-96 bg-gray-200"></div>
      <div class="w-full max-w-sm h-96 bg-gray-200"></div>
      <div class="w-full max-w-sm h-96 bg-gray-200"></div>
      <div class="w-full max-w-sm h-96 bg-gray-200"></div>
    </div>
    <div class="flex justify-between items-center flex-col lg:flex-row">
      <p>點擊數：552 | 上架：2021-11-10 | 上傳者：nnisfjjor</p>
      <DetailOperation />
    </div>
    <DetailTags></DetailTags>
    <MessageBoard />
    <div class="hidden lg:block flex-shrink-0 mt-10 font-medium">
      <p class="text-2xl mb-6">猜你喜歡</p>
      <div class="grid grid-cols-5 gap-5">
        <Card v-for="item in dataSource" :dataSource="item" :key="item.id" />
      </div>
    </div>
    <div class="block lg:hidden flex-shrink-0 mt-10 font-medium">
      <p class="text-2xl mb-6">猜你喜歡</p>
      <div class="grid grid-cols-2 w-full gap-5">
        <template v-for="item in dataSource">
          <div
            :key="item.id"
            v-if="item.type === 'ad'"
            class="h-48 bg-gray-100 flex items-center justify-center col-span-2"
          >
            ad
          </div>
          <Card :key="item.id" :dataSource="item" v-else></Card>
        </template>
      </div>
    </div>
    <div
      class="fixed right-0 w-full bottom-0 lg:w-auto lg:right-36 lg:bottom-40 flex flex-col items-end"
    >
      <div
        class="w-full lg:w-auto bg-gray-700 lg:bg-none text-center flex justify-center"
      >
        <ChangePageComponent
          :hasNextPrevButton="false"
          unit="頁"
          :currentPage="currentPage"
          :totalPage="totalPage"
          @onChangePage="changePage"
        />
      </div>
    </div>
  </div>
</template>
<script>
import MessageBoard from "@/components/MessageBoard";
import DetailOperation from "@/components/DetailOperation";
import DetailTags from "@/components/DetailTags";
import Card from "@/components/Card";
import ChangePageComponent from "@/components/ChangePageComponent";
import mobileMixin from "../../mixin/mobileMixin";
import { pictures } from "../../fakeData";

const fakePictures = pictures.slice(0, 5).map((item) => ({
  ...item,
  path: "/picture",
}));
const mobileFakePictures = pictures
  .slice(0, 8)
  .map((item) => ({
    ...item,
    path: "/picture",
  }))
  .reduce(
    (acc, item, index) => {
      if ((index + 1) % 2 === 0) {
        return [
          ...acc,
          item,
          {
            type: "ad",
          },
        ];
      }
      return [...acc, item];
    },
    [
      {
        type: "ad",
      },
    ]
  );
export default {
  mixins: [mobileMixin],
  components: {
    MessageBoard,
    DetailOperation,
    DetailTags,
    Card,
    ChangePageComponent,
  },
  data() {
    return {
      currentPage: 1,
      totalPage: 10,
      dataSource: fakePictures,
    };
  },
  methods: {
    changePage(page) {
      this.currentPage = page;
    },
    handleAddAds() {
      this.dataSource = mobileFakePictures;
    },
    handleRemoveAds() {
      this.dataSource = fakePictures;
    },
  },
  watch: {
    isMobile: {
      immediate: true,
      handler(newValue, oldValue) {
        if (newValue && !oldValue) {
          this.handleAddAds();
        } else {
          this.handleRemoveAds();
        }
      },
    },
  },
};
</script>
